.container {
  margin-top: 25px;
  margin-bottom: 64px;

  @media (max-width: 1200px) {
    margin-top: 8px;
  }
}

.title {
  margin-top: 20px;
  margin-bottom: 36px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 800;
  font-size: 32px;
  line-height: 39px;
  /* identical to box height */

  letter-spacing: 0.05em;

  @media (max-width: 1200px) {
    margin-bottom: 20px;
  }
}

.mainPost {
  margin-bottom: 40px;
}

.posts {
  display: flex;
  flex-wrap: wrap;
}
.post {
  margin-right: 20px;
  margin-bottom: 40px;

  &:nth-child(3n) {
    margin-right: 0;
  }

  @media (max-width: 1200px) {
    margin-right: 0;
  }
}
