.container {
  position: relative;
  margin-bottom: 16px;
  width: 100%;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;

  margin-bottom: 16px;

  border: 0;

  padding: 0;

  background-color: transparent;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;

  text-decoration: none;

  color: #000;
}
